body, html {
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.main-screen {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

header {
  display: flex;
  justify-content: space-between;
  align-items: center; 
  padding: 10px 30px; 
  background-color: #f8f9fa;
}

header .logo {
  font-size: 24px;
  font-weight: bold;
}

header nav {
  margin-left: auto;
}

header nav ul {
  list-style: none;
  padding: 0;
  display: flex;
  gap: 20px;
}

header nav ul li {
  display: inline;
}

header nav ul li a {
  text-decoration: none;
  color: black;
  font-weight: bold;
}

header .profile {
  font-size: 24px;
  margin-left: 20px;
}

main {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.intro {
  margin-bottom: 40px;
  background: linear-gradient(to right, #f0f9ff, #c9f1fd);
  padding: 60px 40px;
  border-radius: 10px; 
  width: 100vw;
  box-sizing: border-box; 
}

.intro h1 {
  font-size: 36px;
  margin-bottom: 20px;
  text-align: left;
}

.intro p {
  font-size: 18px;
  margin-bottom: 40px;
  text-align: left;
}

#startButton {
  padding: 10px 20px;
  font-size: 20px;
  border-radius: 10px;
  background-color: #333;
  color: white;
  border: none;
  cursor: pointer;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin-right: 1170px;
}

.intro button:hover {
  background-color: #555;
}

.content {
  display: flex;
  gap: 40px;
  margin: 50px 40px;
}

.left-section, .right-section {
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 40px;
}

.weather-section, .travel-section, .restaurant-section {
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.weather-section h2, .travel-section h2, .restaurant-section h2 {
  font-size: 24px;
  margin-bottom: 20px;
}

.weather-section > div, .travel-section > div, .restaurant-section > div {
  display: flex;
  align-items: center;
}